.mainBtn{
  font-family: 'Playfair Display', sans-serif;
  font-weight:300;
  text-decoration:none;
  padding:15px 40px;
  border:1px solid $snow;
  display: inline-block;
  color: #fff;
  font-size: 1.2rem;
  letter-spacing: .15rem;
  transition: all .4s ease-in-out;
  position: relative;
  overflow: hidden;
  z-index: 1;

  @media screen and (max-width:800px){
    padding:10px 30px;
    font-size: 1em;
  }

  &:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: transparent;
    z-index: -2;

  }

  &:before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0%;
    height: 100%;
    background-color: $snow;
    color:$gunmetal;
    transition: all .4s ease-in-out;
    z-index: -1;

  }
  &:hover {
  color: $gunmetal;

  &:before {
  width: 100%;
    }
  }
}

.mainBtn2{
  font-family: 'Playfair Display', serif;
  font-weight:300;
  text-decoration:none;
  padding:15px 40px;
  border:1px solid $gunmetal;
