// HEADER REVEAL
.reveal,
.reveal::after {
	        animation-delay: 0;
	        animation-delay: var(--animation-delay, .5s);
	        animation-iteration-count: 1;
	        animation-iteration-count: var(--iterations, 1);
	        animation-duration: 800ms;
	        animation-duration: var(--duration, 800ms);
	        animation-fill-mode: both;
	        animation-timing-function: cubic-bezier(0.0, 0.0, 0.2, 1);
}
 
.reveal {
	position: relative;
	font-size: 6vw;
	animation-name: text;
	color: $snow;
}
 
.reveal::after {
		content: "";
		position: absolute;
		z-index: 999;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
    background-color: $snow;
	  transform: scaleX(0);
	  transform-origin: 0 50%;
		pointer-events: none;
	  animation-name: revealer;
	}
 
 
 
@keyframes text {
	from {
	        clip-path: inset(0 100% 0 0);
	}
	to {
	        clip-path: inset(0 0 0 0);
	}
}
 
 
 
 
@keyframes revealer {
	
	0%, 50% {
	        transform-origin: 0 50%;
	}
	
	60%, 100% {
	        transform-origin: 100% 50%;		
	}
 
	
	60% {
	        transform: scaleX(1);
	}
	
	100% {
	        transform: scaleX(0);
	}
}

.reveal2,
.reveal2::after {
	        animation-delay: 0;
	        animation-delay: var(--animation-delay, .5s);
	        animation-iteration-count: 1;
	        animation-iteration-count: var(--iterations, 1);
	        animation-duration: 800ms;
	        animation-duration: var(--duration, 800ms);
	        animation-fill-mode: both;
	        animation-timing-function: cubic-bezier(0.0, 0.0, 0.2, 1);
}
 
.reveal2 {
	position: relative;
	font-size: 6vw;
	animation-name: text;
	color: $gunmetal;
}
 
.reveal2::after {
		content: "";
		position: absolute;
		z-index: 999;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
    background-color: $gunmetal;
	  transform: scaleX(0);
	  transform-origin: 0 50%;
		pointer-events: none;
	  animation-name: revealer;
	}
 
 
 
@keyframes text2 {
	from {
	        clip-path: inset(0 100% 0 0);
	}
	to {
	        clip-path: inset(0 0 0 0);
	}
}
 
 
 
 
@keyframes revealer2 {
	
	0%, 50% {
	        transform-origin: 0 50%;
	}
	
	60%, 100% {
	        transform-origin: 100% 50%;		
	}
 
	
	60% {
	        transform: scaleX(1);
	}
	
	100% {
	        transform: scaleX(0);
	}
}